import { auth } from "@/auth"
import { prisma } from "@/lib/prisma"
import { StatsCard } from "@/components/dashboard/stats-card"
import { ActivityFeed } from "@/components/dashboard/activity-feed"
import { QuickActions } from "@/components/dashboard/quick-actions"
import { CheckSquare, Clock, FileText, AlertCircle } from "lucide-react"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"

export default async function DashboardPage() {
    const session = await auth()

    if (!session?.user?.id) return null

    // Parallel data fetching
    const [totalTasks, openTasks, pendingApprovals, timeEntries, recentTasks] = await Promise.all([
        prisma.workItem.count({ where: { assigneeId: session.user.id } }),
        prisma.workItem.count({ where: { assigneeId: session.user.id, status: { not: "DONE" } } }),
        prisma.approvalRequest.count({ where: { status: "PENDING" } }), // naive count
        prisma.timeEntry.findMany({
            where: {
                userId: session.user.id,
                createdAt: { gte: new Date(Date.now() - 7 * 24 * 60 * 60 * 1000) } // Last 7 days
            }
        }),
        prisma.workItem.findMany({
            where: { assigneeId: session.user.id, status: { not: "DONE" } },
            take: 5,
            orderBy: { updatedAt: "desc" },
            include: { project: true }
        })
    ])

    // Calculate total hours
    const totalSeconds = timeEntries.reduce((acc, curr) => acc + curr.durationSeconds, 0)
    const totalHours = (totalSeconds / 3600).toFixed(1)

    return (
        <div className="space-y-8 animate-in fade-in duration-500">
            <div>
                <h1 className="text-3xl font-bold tracking-tight">Insights</h1>
                <p className="text-muted-foreground mt-1">
                    Welcome back, {session.user.name || "User"}. Here's what's happening today.
                </p>
            </div>

            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
                <StatsCard
                    title="My Open Tasks"
                    value={openTasks}
                    icon={CheckSquare}
                    description="Tasks requiring attention"
                />
                <StatsCard
                    title="Time Logged"
                    value={`${totalHours}h`}
                    icon={Clock}
                    description="Past 7 days"
                />
                <StatsCard
                    title="Pending Approvals"
                    value={pendingApprovals}
                    icon={AlertCircle}
                    description="Items waiting review"
                />
                <StatsCard
                    title="Total Assigned"
                    value={totalTasks}
                    icon={FileText}
                    description="All time tasks"
                />
            </div>

            <QuickActions />

            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-7">
                <div className="col-span-4">
                    <ActivityFeed />
                </div>
                <div className="col-span-3">
                    <Card>
                        <CardHeader>
                            <CardTitle>My Tasks</CardTitle>
                            <CardDescription>Recent items assigned to you.</CardDescription>
                        </CardHeader>
                        <CardContent>
                            <div className="space-y-4">
                                {recentTasks.length === 0 ? (
                                    <p className="text-sm text-muted-foreground text-center py-4">No open tasks.</p>
                                ) : (
                                    recentTasks.map(task => (
                                        <div key={task.id} className="flex items-center justify-between border-b pb-2 last:border-0 last:pb-0">
                                            <div className="space-y-1">
                                                <p className="text-sm font-medium leading-none">{task.title}</p>
                                                <p className="text-xs text-muted-foreground">{task.project?.name} â€¢ {task.priority}</p>
                                            </div>
                                            <StatusBadge status={task.status} />
                                        </div>
                                    ))
                                )}
                            </div>
                        </CardContent>
                    </Card>
                </div>
            </div>
        </div>
    )
}

function StatusBadge({ status }: { status: string }) {
    return (
        <span className={`text-[10px] px-2 py-1 rounded-full border ${status === 'DONE' ? 'bg-green-50 text-green-700 border-green-200' :
            status === 'IN_PROGRESS' ? 'bg-blue-50 text-blue-700 border-blue-200' :
                'bg-gray-50 text-gray-600 border-gray-200'
            }`}>
            {status.replace('_', ' ')}
        </span>
    )
}
